<template lang="html">
  <div class="weather">
    <div class="weather-current">
      <p class="weather-current-city" v-text="city"></p>
      <p class="weather-current-temp" v-text="temperature"></p>
      <div :class="['weather-current-icon', current.icon]"></div>
      <p class="weather-current-status" v-text="current.type"></p>
    </div>
    <div class="weather-forecast">
      <div class="weather-forecast-bar">未来天气</div>
      <div class="weather-forecast-body">
        <div class="weather-forecast-body--slide" @click="slideAction(-1)">
          <svg-left color="#fff"></svg-left>
        </div>
        <div class="weather-forecast-wrapper">
          <ul class="weather-forecast-list" :style="{ left: slidePos }">
            <li v-for="item in forecast" class="weather-forecast-item">
              <p class="weather-forecast-item--week" v-text="item.date"></p>
              <p class="weather-forecast-item--temp"><span v-text="item.high"></span><span v-text="item.low" class="is-low"></span></p>
              <div :class="['weather-forecast-item--icon', item.icon]"></div>
              <p class="weather-forecast-item--status" v-text="item.type"></p>
            </li>
          </ul>
        </div>
        <div class="weather-forecast-body--slide is-right" @click="slideAction(1)">
          <svg-right color="#fff"></svg-right>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import SvgLeft from 'components/svg/left.vue'
import SvgRight from 'components/svg/right.vue'

// 获取地址位置
let sc = document.createElement('script')
sc.type = 'text/javascript'
sc.src = 'http://int.dpool.sina.com.cn/iplookup/iplookup.php?format=js'
document.body.appendChild(sc)

export default {
  components: {
    SvgLeft,
    SvgRight
  },
  data () {
    return {
      slide: {
        index: 1,
        unit: 100 / 3
      },
      city: window.remote_ip_info || '杭州',
      forecast: [],
      temperature: ''
    }
  },
  computed: {
    slidePos () {
      return (0 - (this.slide.index - 1) * this.slide.unit) + '%'
    },
    length () {
      return this.forecast.length
    },
    current () {
      if (this.length) {
        return this.forecast[0]
      }
      return {}
    }
  },
  methods: {
    slideAction (one) {
      let { index } = this.slide

      if (index === 1 && one < 0) return
      if (index === this.length - 2 && one > 0) return

      this.slide.index += one
    }
  },
  created () {
    this.$loading.show()

    this.$http.jsonp('http://wthrcdn.etouch.cn/weather_mini', {
      params: {
        city: (window.remote_ip_info && window.remote_ip_info.city) || '杭州'
      }
    })
      .then(res => {
        this.$loading.hide()

        let data = res.body.data
        if (data) {
          this.city = data.city || ''
          this.temperature = data.wendu || ''
          this.forecast = data.forecast.map(item => {
            item.date = item.date.substring(3)
            item.high = item.high.match(/\d+(\.\d+)?/)[0]
            item.low = item.low.match(/\d+(\.\d+)?/)[0]
            switch (item.type) {
              case '小雨':
                item.icon = 's-rainy'
                break
              case '中雨':
                item.icon = 'm-rainy'
                break
              case '大雨':
                item.icon = 'b-rainy'
                break
              case '晴':
                item.icon = 'sunny'
                break
              case '阴':
                item.icon = 'cloudy'
                break
              case '多云':
                item.icon = 'cloudy'
                break
              case '雪':
                item.icon = 'snowy'
                break
              case '多云转晴':
                item.icon = 'cloudy-sunny'
                break
              default:
                item.icon = 'none'
            }
            return item
          })
        }
      }, res => {
        this.$loading.hide()
        this.$modal.alert(res.desc || '请求出错')
      })
  }
}
</script>

<style lang="pcss">
.weather {
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  background: url(./ui/layer.jpg) center no-repeat;
  background-size: cover;
  color: #fff;
  line-height: 1.5;
}

@namespace weather {
  @b current {
    display: flex;
    flex-flow: row wrap;
    align-content: center;
    justify-content: center;
    text-align: center;
    line-height: 1.2;
    height: 60%;

    p, img {
      width: 100%;
    }

    @e city {
      font-size: 56px;
    }

    @e temp {
      font-size: 220px;
      display: flex;
      justify-content: center;
      padding-left: 26px;

      &:after {
        content: '℃';
        font-size: 56px;
        margin-top: 36px;
      }
    }

    @e icon {
      height: 120px;
      width: 120px;
      margin-bottom: 30px;
      background: none center no-repeat;
      background-size: cover;
    }

    @e status {
      font-size: 56px;
    }
  }

  @b forecast {
    height: 40%;
    position: relative;

    @e bar {
      font-size: 40px;
      text-align: center;
      line-height: 100px;
      background-color: rgba(0, 0, 0, 0.7);
    }

    @e body {
      background-color: rgba(0, 0, 0, 0.45);
      position: absolute;
      left: 0;
      top: 100px;
      right: 0;
      bottom: 0;
      display: flex;
      align-items: center;

      @m slide {
        width: 70px;
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: center;

        @when right {
          position: absolute;
          right: 0;
          top: 0;
        }
      }
    }

    @e wrapper {
      position: absolute;
      left: 70px;
      right: 70px;
      top: 0;
      bottom: 0;
      overflow: hidden;
    }

    @e list {
      height: 100%;
      display: flex;
      align-items: center;
      position: relative;
      left: 0;
      transition: all .8s ease;
    }

    @e item {
      width: calc(1/3*100%);
      padding: 0 20px;
      flex-shrink: 0;

      @m week {
        font-size: 34px;
      }

      @m temp {
        font-size: 58px;

        span {
          &:after {
            content: '°';
          }

          @when low {
            margin-left: 10px;
            font-size: 30px;
          }
        }
      }

      @m icon {
        height: 60px;
        width: 60px;
        margin-bottom: 6px;
        background: none center no-repeat;
        background-size: cover;
      }

      @m status {
        font-size: 26px;
      }
    }
  }
}

.none {
  background-color: rgba(0, 0, 0, .5);
}
.sunny {
  background-image: url(./svg/sunny.svg);
}
.s-rainy {
  background-image: url(./svg/s-rainy.svg);
}
.m-rainy {
  background-image: url(./svg/m-rainy.svg);
}
.b-rainy {
  background-image: url(./svg/b-rainy.svg);
}
.cloudy {
  background-image: url(./svg/cloudy.svg);
}
.snowy {
  background-image: url(./svg/snowy.svg);
}
.cloudy-sunny {
  background-image: url(./svg/cloudy-sunny.svg);
}
</style>



